import * as React from 'react';
import Swiper from 'swiper';
import './index.scss';
import swiper1 from '../../images/swiper/swiper1.jpg'
import swiper2 from '../../images/swiper/swiper2.jpg'
import swiper3 from '../../images/swiper/swiper3.jpg'
import swiper4 from '../../images/swiper/swiper4.jpg'

export default class HeaderBar extends React.Component<{}, {}>{
    constructor(props: {}) {
        super(props)
    };

    public componentDidMount() {
        const swiper = new Swiper('.swiper-container', {
            speed: 500,
            autoplay: {
                delay: 7000,
                disableOnInteraction: false
            },
            direction: 'horizontal',
            centeredSlides: true,
            loop: true,
            effect: 'cube',
            cubeEffect: {
                slideShadows: false,
                shadow: false
            },
        });
        console.log(swiper)
    }

    public render() {
        return (
            <div className="swiper-container home-swiper">
                <div className="swiper-wrapper">
                    <div className="swiper-slide">
                        <img src={swiper1} />
                    </div>
                    <div className="swiper-slide">
                        <img src={swiper2} />
                    </div>
                    <div className="swiper-slide">
                        <img src={swiper3} />
                    </div>
                    <div className="swiper-slide">
                        <img src={swiper4} />
                    </div>
                </div>
            </div>
        )
    }
}


